<template>
  <div id="app">
    <div class="container" id="pokemon-block">
      <b-list-group>
        <b-list-group-item>
          <h2 class="mb-3 mt-2">Pokedex</h2>
          <b-input-group class="my-3">
            <button class="btn btn-warning" v-on:click="() => clearInput()">Clear input</button>
            <b-form-input placeholder="Search for pokemons"
                          v-model="search" v-on:keyup.enter="e => searchPokemon(e, search)"></b-form-input>
            <button class="btn btn-primary" v-on:click="e => searchPokemon(e, search)">Search</button>
          </b-input-group>
        </b-list-group-item>
        <b-list-group-item :class="{ hidden: !currentSearch }"
                           class="py-3 d-flex justify-content-between align-items-center" v-for="pokemon in filteredPokemons"
                           :key="'filtered_' + pokemon.number">
          <a :href="pokemon.url" class="pokemon-title text-primary" target="_blank">
            <img :alt="pokemon.name">
          </a>
          <div>
            <img v-bind:src="pokemon.image_front">
          </div>
        </b-list-group-item>
        <b-list-group-item :class="{ hidden: !!currentSearch }"
                           class="py-3 d-flex justify-content-between align-items-center" v-for="pokemon in pokemons"
                           :key="pokemon.number">
          <a :href="pokemon.url"
             class="pokemon-title text-primary" target="_blank">
            <img :alt="pokemon.name">
          </a>
          <div>
            <img v-bind:src="pokemon.image_front">
          </div>
        </b-list-group-item>
      </b-list-group>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'App',
  data: () => ({
    pokemons: [],
    filteredPokemons: [],
    search: '',
    currentSearch: ''
  }),
  mounted() {
    axios
        .get('http://localhost:8080/pokemon/').then(res => {
          console.log(res);
          if(res && res.data && res.data.pokemon_list && res.data.pokemon_list.length) {
            this.pokemons = res.data.pokemon_list;
          }
      });
  },
  methods: {
    clearInput: function () {
      this.filteredPokemons = [];
      this.currentSearch = '';
      this.search = '';
    },
    searchPokemon: function(e, searchValue) {
      searchValue = searchValue.trim();
      if(searchValue) {
        console.log(searchValue);
      }
      axios
          .get(`http://localhost:8080/pokemon/?name=${searchValue}`).then(res => {
            if(res && res.data && res.data.pokemon_list) {
              this.filteredPokemons = this.pokemons.filter(el => el.name.toLowerCase().indexOf(searchValue) > -1);
              this.currentSearch = searchValue;
            }
          });
    }
  }
}
</script>

